"use client"

import { useState } from "react"
import Link from "next/link"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Sheet, SheetContent, SheetTrigger } from "@/components/ui/sheet"
import { logout } from "@/lib/auth"

interface NavHeaderProps {
  userType: "client" | "admin"
}

export function NavHeader({ userType }: NavHeaderProps) {
  const router = useRouter()
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)

  const handleLogout = async () => {
    await logout()
    router.push("/")
  }

  const navItems = userType === "client" 
    ? [
        { label: "Designs", href: "/dashboard" },
        { label: "My Orders", href: "/dashboard/orders" },
        { label: "Measurements", href: "/dashboard/measurements" },
        { label: "Virtual Try-On", href: "/dashboard/virtual-fitting" },
        { label: "Chat", href: "/dashboard/chat" },
      ]
    : [
        { label: "Orders", href: "/admin/dashboard" },
        { label: "Designs", href: "/admin/dashboard/designs" },
        { label: "Customers", href: "/admin/dashboard/customers" },
        { label: "Chat", href: "/admin/dashboard/chat" },
      ]

  return (
    <header className="bg-black text-white py-4">
      <div className="container mx-auto px-4">
        <div className="flex items-center justify-between">
          <Link href={userType === "client" ? "/dashboard" : "/admin/dashboard"} className="text-xl font-bold font-poppins">
            Pa Moris Kargbo
          </Link>

          {/* Desktop Navigation */}
          <nav className="hidden md:flex items-center space-x-6">
            {navItems.map((item) => (
              <Link
                key={item.href}
                href={item.href}
                className="text-gray-300 hover:text-white transition-colors"
              >
                {item.label}
              </Link>
            ))}
            <Button
              onClick={handleLogout}
              variant="outline"
              className="text-white border-white hover:bg-white hover:text-black"
            >
              Logout
            </Button>
          </nav>

          {/* Mobile Navigation */}
          <Sheet open={isMobileMenuOpen} onOpenChange={setIsMobileMenuOpen}>
            <SheetTrigger asChild>
              <Button
                variant="ghost"
                className="md:hidden text-white"
              >
                Menu
              </Button>
            </SheetTrigger>
            <SheetContent side="right" className="w-[300px] bg-black text-white">
              <nav className="flex flex-col space-y-4 mt-8">
                {navItems.map((item) => (
                  <Link
                    key={item.href}
                    href={item.href}
                    className="text-gray-300 hover:text-white transition-colors"
                    onClick={() => setIsMobileMenuOpen(false)}
                  >
                    {item.label}
                  </Link>
                ))}
                <Button
                  onClick={handleLogout}
                  variant="outline"
                  className="text-white border-white hover:bg-white hover:text-black mt-4"
                >
                  Logout
                </Button>
              </nav>
            </SheetContent>
          </Sheet>
        </div>
      </div>
    </header>
  )
}
